.project {
    position: relative;

    position: sticky;
    top: 270px;

    border: 2px solid var(--border-color);

    background-color: var(--bg);

    width: 90%;
    min-width: 90%;
    overflow: hidden;

    // height: fit-content;
    min-height: 400px;
    height: 400px;

    display: flex;
    flex-wrap: nowrap;

    gap: 50px;

    border-bottom: none;

    &:last-child {
        border-bottom: 2px solid var(--border-color);
    }

    .info {
        position: relative;
        width: 50%;
        max-width: 50%;

        background-color: var(--bg);

        padding: 50px 30px 50px 50px;

        display: flex;
        flex-direction: column;

        justify-content: center;
    }

    .langs {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        margin-top: 50px;
    }

    .description {
        text-wrap: pretty;
        color: gray;
        font-size: 1.2rem;
        font-weight: 400;
    }

    .title {
        img {}

        h3 {
            font-size: 1.6rem;
            font-weight: 600;
            margin-bottom: 10px;
        }

        margin-bottom: 5px;
    }

    .right {
        position: relative;

        min-width: 50%;
        max-width: 50%;

        width: 50%;
        height: 100%;

        display: flex;
        justify-content: center;
        align-items: center;

        img {
            object-fit: fill;
            height: 100%;
            width: 100%;

            transition: all .3s ease;

            &:hover {
                transform: scale(1.1);
            }
        }
    }
}


@media (max-width: 1178px) {
    .project {
        width: 45%;
    }
}

@media (max-width: 948px) {
    .project {
        width: 80%;

        &:hover {
            transform: none;
        }
    }
}


@media (max-width: 630px) {
    .project {

        width: 100%;
    }
}