.header {
    display: flex;
    position: fixed;
    width: 90%;
    justify-content: space-between;

    align-items: center;
    z-index: 10;

    padding: 20px 100px;
    transition: all .3s ease;

    &.fixed {
        padding: 20px 50px;
        width: 100%;

        background-color: var(--bg);
        border-bottom: 2px solid var(--border-color);
    }
}


@keyframes show-header {
    0% {}

    100% {
        transform: translate(0, 0);
    }
}

@keyframes hide-header {
    0% {
        left: 0;
        width: 100%;
        position: absolute;
        transform: translate(0, 0);

        background-color: var(--bg-header);
        border-bottom: 2px solid var(--border-color);
    }

    100% {
        display: flex;
        position: fixed;
        width: 90%;
        justify-content: space-between;

        background-color: var(--bg-header);
        border-bottom: 2px solid var(--border-color);
        // transform: translateY(-120%);
    }
}

.links {
    display: flex;
    gap: 30px;
}

.link {
    img {
        max-height: 30px;
        filter: var(--link-filter);
    }
}

@media (max-width: 948px) {
    .link img {
        height: 35px;
    }

    .header {
        padding: 15px 20px;
        width: 100%;
        left: 0;
    }
}