.skills-wrapper {
    height: 100%;
    width: 100%;

    margin: 0 auto;

    transition: background-color .3s ease;

    .title {
        width: 100%;
        margin: 0 auto;

        border-bottom: 3px solid var(--border-color);
        padding: 20px 0;
    }
}

.skills_inner {

    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;

    gap: 15px 20px;
    padding: 40px 30px 0px 0px;
}

@media (max-width: 630px) {
    .skills_inner {
        padding: 40px 10px 0px 0px;
    }
}