@import '../../var.scss';

.projects {
    position: relative;
    display: flex;
    flex-wrap: wrap;

    justify-content: flex-start;

    transition: all .3s ease;
}

.empty {
    font-size: 30px;
    height: 7.5em;

    margin: 0 auto;
    padding-top: 60px;

    width: fit-content;
    text-align: center;
}

.projects_wrapper {
    position: sticky;
    top: 80px;

    width: 100%;

    margin-top: 420px;

    .projects-title {
        width: 100%;

        padding: 20px 0;
        border-bottom: 2px solid var(--border-color);
    }

    &.hide {
        transition: all .3s ease;
        transform: translateY(-120%);
    }
}


.projects-anim-enter {
    opacity: 0;
    transform: translateX(-200%);
    transition: none;
}

.projects-anim-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: all $anim-time $anim-delay ease;
}

.projects-anim-enter-done {

    opacity: 1;
    transform: translateX(0);
    transition: all .5s ease;
}


@media (max-width: 948px) {
    .projects_wrapper {
        margin-top: 200px;
    }
}