@import '../../var.scss';

.education {
    position: relative;
    margin-top: 380px;
    width: 100%;

    display: block;

    $w: 40px;

    opacity: 0;

    &_wrapper {
        display: flex;
        flex-wrap: wrap;

        gap: 100px;
        row-gap: 20px;

        width: 100%;
    }

    &__title {
        position: relative;

        width: fit-content;

        font-size: 2rem;
        font-weight: 600;

        transform: translateX($w + 10px);

        margin-bottom: 50px;

        .pre_line {
            display: block;
            width: $w;
            height: 5px;
            border-radius: 5px;
            background-color: #f1c40f;

            position: absolute;
            top: 50%;
            left: -$w - 10px;

            transform: translateY(-50%);
        }
    }

    &_element {
        display: inline-flex;
        flex-wrap: nowrap;
        align-items: baseline;

        width: fit-content;
        position: relative;

        margin-bottom: 30px;

        &__date {
            width: fit-content;
            text-wrap: nowrap;

            border-radius: 5px;
            font-weight: 600;

            padding: 5px 10px;

            margin-right: 30px;

            background-color: var(--bg-skill);
        }

        &_content {
            &__level {
                font-size: 1.5rem;
                font-weight: 600;
            }

            &__where {
                font-size: 1.5rem;
                font-weight: 500;

                color: gray;
                margin-top: 10px;
            }
        }
    }
}



.education-anim-enter {
    opacity: 0;
    transform: translateX(-200%);
    transition: none;
}

.education-anim-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: all $anim-time $anim-delay ease;
}

.education-anim-enter-done {
    opacity: 1;
    transform: translateX(0);
    transition: all $anim-time ease;
}


@media (max-width: 630px) {
    .education {
        margin-top: 200px;
    }
}