.filter {
    position: relative;
    display: flex;

    margin-top: 30px;
    margin-bottom: 30px;

    width: 100%;

    gap: 30px;
}

.filter-item {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 10px 30px;

    background-color: var(--bnt-filter-color);
    border: 2px solid transparent;
    border-radius: 10px;

    font-size: 16px;

    text-transform: capitalize;
    transition: all .3s ease;

    &:last-child {
        margin-right: 0;
    }

    &:hover {
        border-color: rgb(148, 148, 148);
    }

    &.selected {
        border-color: var(--border-color);
    }
}

@media (max-width: 505px) {
    .filter-item {
        padding: 0 20px;
        margin-right: 20px;
    }
}

@media (max-width: 370px) {
    .filter-item {
        padding: 0 15px;
        margin-right: 15px;
    }
}