.skill {
    user-select: none;
    position: relative;

    color: var(--text-color);

    border-radius: 7px;
    background-color: var(--bg-skill);

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;

    min-width: 160px;
    font-size: 16px;
    height: 40px;

    padding: 0px 20px;

    img {
        height: 70%;
        filter: var(--link-filter);
    }
}

@media (max-width: 630px) {
    .skill {
        padding: 0 15px;
    }
}