:root {
    --flag-w: 30px;
}

.lang_selector {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.lang_selector_img {
    filter: var(--link-filter);
    height: 30px;
}

.dropdown_wrapper {
    position: absolute;
    bottom: -110px;
    padding-top: 110px;
    width: fit-content;

    opacity: 0;
    pointer-events: none;

    z-index: -1;

    transition: all .4s ease;

    &.visible {
        opacity: 1;
        display: flex;
        pointer-events: all;

        z-index: 20;
    }
}

.dropdown {
    display: flex;

    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: calc(var(--flag-w) / 2);

    z-index: 20;
    bottom: 0;
    padding: calc(var(--flag-w) / 1.5) calc(var(--flag-w) / 2) calc(var(--flag-w) / 2) calc(var(--flag-w) / 2);
    border-radius: 5px;

    background-color: var(--bg-skill);

    .component {
        transition: transform .2s ease;

        img {
            width: var(--flag-w);
            border-radius: 3px;
        }

        &:hover {
            transform: scale(1.17);
        }
    }
}


@keyframes show {
    0% {
        display: flex;
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@media (max-width: 948px) {
    .lang_selector_img {
        height: 35px;
    }

    :root {
        --flag-w: 40px;
    }

    .dropdown_wrapper {
        bottom: -130px;
        padding-top: 130px;
    }
}